.App {
  width: 100vw;
}

h1{
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  text-align: center;
  font-size: 4rem;
  margin: 50px;
}

.content {
  width: 500px;
  margin: auto;
}


.content > * {
  width: 100%;
  margin: 10px;
}

@media (max-width:600px) {
  .content {
    width: 95vw;
    margin: auto;
  }

  .content > * {
    margin: 0;
  }
}


.row{
  display: flex;
}

.row > *{
  flex: 1;
}

.pixelsgrid {
  display: grid;
  grid-template-rows: repeat(auto-fill,1fr);
  height: 500px;
  background-color: #fff;
}

@media (max-width:600px) {
  .pixelsgrid {
    
    height: 95vw;
    
  }
}

.cell {
  border: 0.1px solid #000;
  height: 100%;
  cursor: pointer;
  transition: transform .1s ease;
}

.cell:hover{
  transform: scale(1.1);
}

.controls{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (max-width:600px) {
  .controls{
    flex-wrap: wrap;
    justify-content: center;
  }

  .controls > *{
    margin: 5px;
  }
}

.btn{
  border: none;
  cursor: pointer;
  padding: 10px;
  background-color: brown;
  color: #fff;
  font-size: 18px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  transition: background-color .3s;
}

.btn:hover{
  background-color: rgb(207, 76, 76);
}

.color{
  width: 30px;
  height: 30px;
  border: 1px solid #000;
  cursor: pointer;
}

.btn--download{
  display: flex;
  align-items: center;
  justify-content: center;
}


.btn--download img{
  width: 20px;
  fill: #fff;
}

.custom,
.custom input{
  font-size: 16px;
  text-align: center;
  padding: 5px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.custom label > *{
  display: block;
}

.btn--set{
  margin: 5px 0;
  padding: 5px;
  width: 100%;
}

.errorBorder{
  border: 2px solid red;
}

input{
  outline: none;
}

.currentColor{
  height: 50px;
  width: 50px;
}

